/* Overlay closing cross */
.overlay .overlay-close {
  width: 80px;
  height: 80px;
  position: absolute;
  right: 20px;
  top: 20px;
  overflow: hidden;
  border: none;
  background: image-url("icons/cross.png") no-repeat center center;
  text-indent: 200%;
  color: transparent;
